import { Component, OnInit, Output, EventEmitter, Input, ElementRef, ViewChild } from '@angular/core';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

@Component({
  selector: 'app-search-product',
  templateUrl: './search-product.component.html',
  styleUrls: ['./search-product.component.scss']
})
export class SearchProductComponent {
  @ViewChild("inputSearchIngredient") myInputField: ElementRef;
  @Input("searchProductList") searchProductList: boolean = true;
  @Output("closeSearchProductList") closeSearchProductList = new EventEmitter<any>();
  public typeProducts: 1 | 2 | 3 = 3;
  public ingredientList: any[] = this.vhQueryCafe.getlocalDetailProducts().filter(e => e.type == 3).map(item => {return {...item, ...this.vhQueryCafe.getUnit_byRatio(item.units, item.units.find(u => u.default).ratio)}})
  public searchList: any[] = [];
  search: '';
  id_branch: string = this.vhQueryCafe.getDefaultBranch()._id
  constructor(
    private vhComponent: VhComponent,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm
  ) {
    this.searchList = [... this.ingredientList];
  }

  cancel(value) {
    if (value) this.vhComponent.hideModal()
  }
  selectProducts(value) {
    if (value) this.vhComponent.hideModal(value)
  }

  searchProduct(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.ingredientList, ['name'])
    else this.searchList = this.ingredientList
  }
  chooseProduct(item) {
    document.getElementById("inputSearchIngredient").blur();
    this.closeSearchProductList.emit(item);
  }
  goBack() {
    document.getElementById("inputSearchIngredient").blur();
    this.closeSearchProductList.emit(false);
  }
  ngAfterViewInit() {
    document.getElementById("inputSearchIngredient").focus();
  }
  ngAfterViewChecked() {
    if (document.querySelector(".ant-drawer-body") && document.getElementById("inputSearchIngredient") && document.getElementById("infinite-container")) {
      document.getElementById("infinite-container").style.height = document.querySelector(".ant-drawer-body").clientHeight - document.getElementById("inputSearchIngredient").clientHeight - 60 + "px";
    }
  }
}
