import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

@Component({
  selector: 'app-staff',
  templateUrl: './staff.component.html',
  styleUrls: ['./staff.component.scss']
})
export class StaffComponent {
  @Input("id_employee") id_employee;
  @Output("closeAndReceiveEmployee") closeAndReceiveEmployee = new EventEmitter<any>();
  public employeeList: any[] = this.vhAuth.getlocalEmployees();
  public searchList: any[] = [];
  search: string;
  constructor(
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhAuth: VhAuth
  ) {
    this.searchList = [...this.employeeList];
    if (!this.id_employee && this.searchList.length) {
      this.id_employee = this.searchList[0]._id
    }
  }
  cancel(value) {
    if (value) this.closeAndReceiveEmployee.emit(value);
  }
  selectEmployees(value) {
    if (value) this.vhComponent.hideModal(value)
  }

  searchBar(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.employeeList, ['name'])
    else this.searchList = this.employeeList
  }
  goBack() {
    this.closeAndReceiveEmployee.emit(false)
  }

}
