import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-info-supplier',
  templateUrl: './info-supplier.component.html',
  styleUrls: ['./info-supplier.component.scss']
})
export class InfoSupplierComponent implements OnInit {

  @Input("visibleInfoSupplier") visibleInfoSupplier: boolean = false;
  @Input("supplier") supplier;
  @Output("closeInfoSupplier") closeInfoSupplier = new EventEmitter<any>();
  constructor() { }

  ngOnInit(): void {
  }
  goBack() {
    this.closeInfoSupplier.emit(false)
  }

}
