import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { NzCascaderOption } from 'ng-zorro-antd/cascader';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-tax',
  templateUrl: './tax.component.html',
  styleUrls: ['./tax.component.scss']
})
export class TaxComponent implements OnInit {
  @Input("id_supplier") id_supplier = 'id_retail';
  @Input("payment_type") payment_type = 1;
  @Input("id_wallet") id_wallet = 'id_cash';
  @Output('closeAndGetValuePayment') closeAndGetValuePayment = new EventEmitter<any>();
  constructor(
    private vhQueryCafe: VhQueryCafe,
    private languageService: LanguageService,
    private vhAuth: VhAuth
  ) { }
  ngOnInit(): void {
    this.listWallets = this.vhQueryCafe.getlocalWallets().filter(wallet => wallet._id != 'id_cash' && wallet.id_branch == this.vhQueryCafe.getDefaultBranch()._id)
      .map(wallet => { return { label: wallet.name, value: wallet._id, isLeaf: true } })
    this.nzOptions[2].children = this.listWallets;
    if (this.payment_type) {
      if (this.payment_type == 2 || this.payment_type == 1) this.type_payment = [this.payment_type]
      else if (this.payment_type == 3) this.type_payment = [3, this.id_wallet];
    }
  }
  type_payment: any[]
  listWallets: Array<any> = [];

  nzOptions: NzCascaderOption[] = [
    {
      value: 1,
      label: this.languageService.translate("Cash"),
      isLeaf: true
    },
    {
      value: 2,
      label: this.languageService.translate("Debit"),
      disabled: false,
      isLeaf: true
    },
    {
      value: 3,
      label: this.languageService.translate("Account"),
      children: []
    }]

  onChanges(event) {
    this.closeAndGetValuePayment.emit(event);
  }

}
