import { Component, EventEmitter, Input, Output } from '@angular/core';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';

@Component({
  selector: 'app-select-supplier',
  templateUrl: './select-supplier.component.html',
  styleUrls: ['./select-supplier.component.scss']
})
export class SelectSupplierComponent {
  @Input("id_supplier") id_supplier;
  @Output() closeAndReceiveSupplier = new EventEmitter<any>();
  public supplierList: any[] = this.vhQueryCafe.getlocalSuppliers();
  public searchList: any[] = [];
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe
  ) {
    this.searchList = [...this.supplierList];
  }
  selectSuppliers(value) {
    this.closeAndReceiveSupplier.emit(value);
  }
}
